* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.App {
  margin: 1rem;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  border: 10px solid black;
  padding-bottom: 1rem;
  
}

.title {
  text-align: center;
  color: white;
  background-color: black;
} 

.addedColor {
  margin: 0.5rem 1rem 0.5rem 1rem;
  padding: 0.5rem;
  border-radius: 5px;
  color: white;
  background-color: black;
  cursor: pointer;
  border: solid 1px white;
}

.listRandomColor {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  list-style: none;
  margin: 0.5rem 1rem 0.5rem 1rem;
  width: 100%;
  
}
.listRandomColor_li {
  height: 50px;
  width: 100px;
  margin: 2px;
  border-radius: 5px;
}
.boxDrop {
  border: 6px solid black;
  border-radius: 5px;
  /*width: 100%;*/
  height: 300px;
  margin: 0.5rem 1rem 0.5rem 1rem;
  display: flex;
  flex-direction: column;
  
}
.ulBoxDrop {
  display: flex;
  flex-direction: column;
}
.listBoxDrop {
  list-style: none;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding: 0.5rem;
}

.deleteButton {
  width: 20px;
  border: solid 1px white;
  border-radius: 20%;
  font-weight: bold;
}

.storm {
  text-align: center;
  font-size: 2em;
}